export default {
	data() {
		return {
			currentDate: new Date().getTime(),
			minDate: new Date().getTime(),
			formatter(type, value) {
			  if (type === 'year') {
				return `${value}年`;
			  } else if (type === 'month') {
				return `${value}月`;
			  }
			  return value;
			},
			backUrl:"",
			isShow: false,
		}
	},
	methods: {
		onChange(){
			this.isShow = true;
		},
		onInput(event) {
			this.setData({
			  currentDate: event.detail
			});
		},
		toTask(){
			uni.reLaunch({
			    url: '/pages/task/index'
			});
		}
	},
	onLoad(option){
		if(option.backpath){
			this.backUrl = option.backpath;
		}
	}
}
